@media only screen and (max-width: 600px) {
  .charts-container {
    flex-direction: column;
    align-items: center;
  }
  .charts-data {
    width: 100%;
  }
  .charts-wrapper {
    flex-direction: column;
  }
  text {
    font-size: 18px;
    text-shadow: 0px 1px 5px black;
  }
}
